a,
a:visited {
    text-decoration: none
}

.cont,
table {
    border-collapse: collapse
}

.agreement .modal .btn-agree,
.agreement .modal h3,
.btns td,
.count .comment,
.loan-li .img_box,
.nav .nav_tab td,
