html {
    height: 100%;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -webkit-font-smoothing: antialiased;
    font-family: PingFangSC-Regular, 'Hiragino Sans GB', 'Source Han Sans CN', 'noto sans', "Helvetica Neue", Helvetica, Rotobo
}

body,
dd,
div,
dl,
dt,
form,
h1,
h2,
h3,
h4,
h5,
h6,
input,
legend,
li,
ol,
p,
td,
textarea,
th,
ul {
    margin: 0;
    padding: 0
}

a,
button,
input,
select,
textarea {
    outline: 0
}

img {
    border: 0;
    vertical-align: middle
}

em,
i {
    font-style: normal
}

textarea {
    resize: none
}

a {
    color: #373737;
    -webkit-tap-highlight-color: rgba(255, 0, 0, 0)
}

ol,
ul {
    list-style: none
}

h1,
h2,
h3,
h4,
h5 {
    font-size: 14px
}

input,
label,
select {
    vertical-align: middle;
    font: 100% \5FAE\8F6F\96C5\9ED1
}

.loan-li,
.loan-logo {
    vertical-align: top;
    display: inline-block
}

.content ul li {
    padding: 1% 0;
    clear: both
}

.content ul li:active {
    background-color: #F0F0F2
}

* {
    -webkit-tap-highlight-color: transparent
}

body {
    color: #333;
    margin: 0 auto;
    padding-bottom: .6rem
}

.bd {
    position: fixed;
    height: 2rem;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0
}

.clear {
    clear: both;
    height: .8rem
}

.loan-logo,
.loan-logo img {
    width: 1.6rem;
    height: 1.6rem
}

.loan-logo {
    position: relative;
    background-color: #f5f5f5;
    border-radius: .067rem;
    margin-top: .38rem
}

.loan-li {
    padding: .63rem 0 0 .467rem;
    min-width: 70%
}

.loan-li .img_box {
    width: 96px;
    height: 96px;
    margin-top: -25px
}

.loan-li .color {
    color: #565f68;
    padding-left: 5px
}

.loan-li .loan-t {
    font-size: .4rem;
    height: .743rem
}

.loan-li p {
    font-size: .34rem;
    height: .69rem;
    color: #565f68;
    line-height: .32rem
}

.loan-li .loan-t em,
.loan-li .loan-t i {
    display: inline-block;
    vertical-align: middle;
    color: #373737
}

.loan-li .tro {
    width: 11px;
    height: 11px;
    position: absolute;
    top: 50%;
    right: 20px;
    margin-top: -8px;
    border: 2px solid #b9b9b9;
    border-left: 0;
    border-bottom: 0;
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg)
}

.nav,
.nav .nav_tab,
.nav a {
    width: 100%
}

.fixed,
.nav1 {
    position: fixed;
    top: 0
}

.fixed {
    left: 0;
    z-index: 10000;
    box-shadow: 0 0 7px #d3d3d3;
    -webkit-animation: FIXED .3S;
    animation: FIXED .3S
}

@-webkit-keyframes FIXED {
    0% {
        -webkit-transform: translateY(-100%);
        transform: translateY(-100%)
    }
    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0)
    }
}

.nav1 {
    display: none;
    z-index: 1001
}

.icon-img,
.nav a {
    position: relative
}

.nav .nav_tab td:first-child {
    border-left: 0
}

.nav .nav_tab td:last-child {
    border-right: 0
}

.nav a {
    display: block;
    padding: 0 3px;
    height: 1.12rem;
    line-height: 1.12rem;
    font-size: .37rem;
    background: #FFF;
    box-sizing: border-box;
    border-bottom: 1px solid #EBEBEE
}

.nav .long-tab {
    padding: 0
}

.nav a.actived {
    color: #f95c06;
    border-bottom: 2px solid #f95c06
}

.nav-m {
    width: .48rem
}

.count span {
    color: #f95c06;
    padding-right: 5px
}

