.icon-img {
    width: .435rem;
    height: .427rem;
    margin-left: .13rem;
    border-radius: .05rem;
    vertical-align: middle
}

.rm {
    background: #ff9160
}

.tj {
    background: #4da9ec
}

.xp {
    background: #ffbb38
}

.hb,
.qtmx {
    background: #ff7373
}

.hb:before,
.qtmx:before,
.rm:before,
.tj:before,
.xp:before {
    display: inline-block;
    width: .435rem;
    height: .427rem;
    line-height: .435rem;
    overflow: hidden;
    font-size: .3rem;
    content: '';
    text-align: center;
    vertical-align: middle;
    color: #fff
}

.tj:before {
    content: '荐'
}

.rm:before {
    content: '热'
}

.xp:before {
    content: '新'
}

.hb {
    width: .747rem
}

.hb:before {
    width: .747rem;
    content: '红包'
}

.qtmx {
    width: 1.25rem
}

.qtmx:before {
    width: 1.25rem;
    content: '7天免息'
}

.agreement:after,
.comment:after {
    content: ""
}

.item-ad,
.item-c,
.item-s {
    position: relative;
    border-bottom: 1px solid #ebebee;
    padding-right: .48rem;
    margin-left: .48rem;
    box-sizing: border-box;
    height: 2.367rem
}

.comment:after,
.count .comment {
    width: 1.12rem;
    position: absolute
}

.hideComment .count span.comment {
    display: none
}

.btns td a,
.btns td p,
.count .comment {
    display: inline-block
}

.count .comment {
    height: .533rem;
    line-height: .533rem;
    color: #ff9060;
    border: 1px solid #ff9060;
    font-size: .33rem;
    border-radius: .067rem;
    right: .48rem;
    bottom: .533rem;
    padding: 0
}

.comment:after {
    height: 1.12rem;
    top: -.24rem;
    right: 0
}

.btns {
    width: 100%;
    padding: 0 .48rem;
    border-bottom: .2rem solid #f7f7f7
}

.btns td {
    padding: 0
}

.btns td p {
    padding: .76rem 0 0 .1rem;
    font-size: .32rem;
    color: #757575;
    vertical-align: top
}

.btns td a {
    margin: .347rem auto .16rem;
    width: 1.093rem;
    height: 1.093rem;
    background: url(https://res.cardniu.com/daikuan/loan/wx/images/btns.png) no-repeat;
    background-size: 300% 100%;
    position: relative
}

.btns td a.btn1 .dot,
.loading {
    position: absolute;
    display: none
}

.btns td a.btn1 {
    background-position: left 0
}

.btns td a.btn4 {
    background-position: center 0
}

.btns td a.btn3 {
    background-position: right 0
}

.btns td a.btn1 .dot {
    width: .867rem;
    height: .293rem;
    background: url(https://res.cardniu.com/daikuan/loan/wx/images/qipao1.png) no-repeat;
    background-size: 100% 100%;
    top: .1rem;
    right: -.95rem;
    -webkit-animation: QIPAO 1S;
    animation: QIPAO 1S;
    z-index: 1000
}

@-webkit-keyframes QIPAO {
    0% {
        -webkit-transform: scaleX(.7);
        transform: scaleX(.7)
    }
    12.5% {
        -webkit-transform: scaleX(1.3);
        transform: scaleX(1.3)
    }
    25% {
        -webkit-transform: scaleX(.8);
        transform: scaleX(.8)
    }
    37.5% {
        -webkit-transform: scaleX(1.2);
        transform: scaleX(1.2)
    }
    50% {
        -webkit-transform: scaleX(.85);
        transform: scaleX(.85)
    }
    62.5% {
        -webkit-transform: scaleX(1.12);
        transform: scaleX(1.12)
    }
    75% {
        -webkit-transform: scaleX(.9);
        transform: scaleX(.9)
    }
    87.5% {
        -webkit-transform: scaleX(1.05);
        transform: scaleX(1.05)
    }
    100% {
        -webkit-transform: scaleX(1);
        transform: scaleX(1)
    }
}

.loading {
    top: 50%;
    left: 50%;
    margin-left: -.2rem;
    margin-top: -.2rem;
    background: url(https://res.cardniu.com/daikuan/loan/wx/images/loading.gif) no-repeat;
    background-size: 100% 100%;
    width: .4rem;
    height: .4rem
}

.hide,
.knloan [data-knsty="1"],
.ssjloan [data-ssjsty="1"],
.zxloan [data-zxsty="1"] {
    display: none
}

[data-dpr="3"] .hb:before,
[data-dpr="3"] .rm:before,
[data-dpr="3"] .tj:before,
[data-dpr="3"] .xp:before {
    line-height: .46rem
}

[data-dpr="3"] .count .comment {
    line-height: .555rem!important
}

.agreement:after {
    position: fixed;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: .6;
    z-index: 99998
}

.agreement .modal {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 89.2%;
    padding-top: .906rem;
    margin: -3rem 0 0 -44.6%;
    background: #fff;
    border-radius: .133rem;
    border: 1px solid #e8e8e8;
    z-index: 99999
}

.agreement .modal .close {
    position: absolute;
    right: .24rem;
    top: .01rem;
    font-size: .8rem;
    color: #bfbfbf
}

.agreement .modal h3 {
    font-weight: 700;
    font-size: .48rem
}

.agreement .modal .btn-agree,
.agreement .modal h4 {
    width: 7.68rem;
    font-size: .4rem;
    font-weight: 400;
    display: block
}

.agreement .modal h4 {
    margin: .467rem auto;
    text-align: left;
    line-height: 1.3
}

.agreement .modal h4 a {
    color: #59afe1
}

.agreement .modal .btn-agree {
    margin: 0 auto .906rem;
    height: 1.12rem;
    line-height: 1.12rem;
    background: #ff9352;
    color: #fff;
    border-radius: .106rem
}

.footer,
.tips {
    font-size: .3rem;
    text-align: center
}

.tips {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    line-height: .6rem;
    background: #fff7ea;
    color: #ffa920
}

.footer {
    line-height: 1.5;
    margin: 15px 0 5px;
    color: #b7b7b7
}

.footer .company {
    display: block;
    text-align: left;
    padding: 0 16px;
    text-indent: 2em
}

.footer .last {
    margin-bottom: 16px
}